.footer {
  position: relative;
  bottom: 0;

  width: 100vw;
  height: 80px;

  background-color: $main-color;
  line-height: 80px;
  font-size: 11px;

  &__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $white-color;
  }
}

@media screen and(min-width: $large-screen) {
  .footer {
    width: 100%;
    margin: auto;
  }
}

@media screen and(min-width: 756px) {
  .footer__text {
    font-size: 15px;
  }
}
